
import React, { useState } from 'react';

const Contact=()=>{
    const [data,setData]=useState({
        fullname:'',
        phone:'',
        email:'',
        msg:'',
    });

    const InputEvent=(event)=>{
        const {name,value}=event.target;
        setData((preVal)=>{
            return{
                ...preVal,
                [name]:value,  /*  means if there is change in old and new data entered then it will save */
            };
        });
    };

    const formSubmit=(e)=>{
        e.preventDefault();
        alert( `${data.fullname} your form has been submitted SUCCESSFULLY!!`)

    }


    return (
        <>
        <div className='my-5'>
        <h1 className='text-center '> contact us</h1>
        </div>
        <div className='container contact_div'>
            <div className='row'>
                <div className='col-md-6 col-10 mx-auto'>
                    <form onSubmit={formSubmit}>
                        <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Full Name</label>
                        <input type="text" 
                        class="form-control" 
                        id="exampleFormControlInput1" 
                        name='fullname'
                        value={data.fullname}
                        onChange={InputEvent}
                        placeholder="write your name"/>
                        </div>
                        <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Phone no.</label>
                        <input type="text" 
                        class="form-control" 
                        id="exampleFormControlInput1" 
                        name='phone'
                        value={data.phone}
                        onChange={InputEvent}
                        placeholder="write your phone number"/>
                        </div>
                        <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Email</label>
                        <input type="email" 
                        class="form-control"
                        id="exampleFormControlInput1"
                        name='email'
                        value={data.email}
                        onChange={InputEvent} 
                        placeholder="write your email"/>
                        </div>                        
                        <div class="mb-3">
                        <label for="exampleFormControlTextarea1" class="form-label">Text area</label>
                        <textarea class="form-control" id="exampleFormControlTextarea1" 
                        name='msg'
                        value={data.msg}
                        onChange={InputEvent} 
                        rows="3"></textarea>
                        </div>
                        <div class="col-12">
                          <button class="btn btn-primary" type="submit">Submit form</button>
                          </div>
                    </form>

                </div>
            </div>
        </div>
        </>
    );
};

export {Contact};